* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }
.App {
  position: relative;
  // width: 100%
  // height: 100%
  // background-image: url(./img/Background.jpeg)
  // background-attachment: fixed
  // background-position: center
  background-repeat: repeat;
  background-size: cover;
  transition: 3s;
  .background {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    z-index: -50; }
  header {
    position: relative;
    height: 40vh;
    nav {
      .brand {
        .kangaroo {
          position: absolute;
          left: 15px;
          top: 10px;
          height: 60px; } }
      button {
        position: fixed;
        right: 5vw;
        top: 3vh;
        z-index: 51;
        border: none;
        background-color: rgba(0,0,0,0);
        .burgerImg {
          height: 6vh; } }
      button:hover {} }
    .main-logo {
      position: absolute;
      left: 50%;
      top: 60%;
      transform: translate(-50%, -50%);
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px;
      h1 {
        font-size: 25px; }
      h2 {
        font-size: 46px; } } }
  main {
    .image-box {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      align-content: space-around;
      div {
        padding: 10px 20px;
        flex-basis: 100%;
        // @media (min-width: 576px)
        //   flex-basis: 45%
        // @media (min-width: 768px)
        //   flex-basis: 33%
        img {
          width: 100%; } } } }
  section.about-me {
    position: relative;
    min-height: 100vh;
    .about-me-wrap {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 75vw;
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px;
      // background-color: #ffffff
      .top {
        h2 {
          font-size: 23px; }
        h1 {
          font-size: 49px; } }
      .body {
        // position: absolute
        // left: 50%
        // top: 25%
        // transform: translate(-50%, -50%)
        font-family: 'Permanent Marker', cursive;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 3px;
 } } }        // background-color: #666666
  footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    // transform: translate(-50%, -50%)
    background-color: #000000;
    p {
      margin-top: 12px;
      line-height: 10px;
      color: #6969696e;
      font-size: 10px;
      letter-spacing: 3px;
      font-family: Arial, "Helvetica Neue", Helvetica, sans-serif; } } }
