@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.App {
  position: relative;
  background-repeat: repeat;
  background-size: cover;
  transition: 3s; }
  .App .background {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    z-index: -50; }
  .App header {
    position: relative;
    height: 40vh; }
    .App header nav .brand .kangaroo {
      position: absolute;
      left: 15px;
      top: 10px;
      height: 60px; }
    .App header nav button {
      position: fixed;
      right: 5vw;
      top: 3vh;
      z-index: 51;
      border: none;
      background-color: rgba(0, 0, 0, 0); }
      .App header nav button .burgerImg {
        height: 6vh; }
    .App header .main-logo {
      position: absolute;
      left: 50%;
      top: 60%;
      transform: translate(-50%, -50%);
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .App header .main-logo h1 {
        font-size: 25px; }
      .App header .main-logo h2 {
        font-size: 46px; }
  .App main .image-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-around; }
    .App main .image-box div {
      padding: 10px 20px;
      flex-basis: 100%; }
      .App main .image-box div img {
        width: 100%; }
  .App section.about-me {
    position: relative;
    min-height: 100vh; }
    .App section.about-me .about-me-wrap {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 75vw;
      font-family: 'Permanent Marker', cursive;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .App section.about-me .about-me-wrap .top h2 {
        font-size: 23px; }
      .App section.about-me .about-me-wrap .top h1 {
        font-size: 49px; }
      .App section.about-me .about-me-wrap .body {
        font-family: 'Permanent Marker', cursive;
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 3px; }
  .App footer {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-color: #000000; }
    .App footer p {
      margin-top: 12px;
      line-height: 10px;
      color: #6969696e;
      font-size: 10px;
      letter-spacing: 3px;
      font-family: Arial, "Helvetica Neue", Helvetica, sans-serif; }

ul.menu-hamburger {
  position: fixed;
  z-index: 50;
  left: 100%;
  top: 0;
  padding-top: 23vh;
  list-style: none;
  text-align: center;
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: #000000;
  list-style: none;
  transition: 1s;
}

li {
  list-style: none;
  margin-bottom: 13vh;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-size: 1.7rem;
}

.active {
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 23vh;
  list-style: none;
  text-align: center;
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: #000000;
  z-index: 50;
  transition: 1s;
}
